.Tabs {
  position: relative;
  margin-bottom: 40px;
  text-align: center;
}

.Tabs__Tab {
  display: inline-block;
  padding: 1em;
  background: transparent;
  color: rgb(37, 37, 37);
  border: 1px solid #ffc210;
  cursor: pointer;
  border-radius: 4px;
  margin: 0px 10px;
  font-family: Roboto;
  font-size: 18px;
  min-width: 190px;
  text-align: center;
  font-weight: bold;
  margin-top: 16px;
}

.Tabs__Tab--active {
  background: #ffc210;
  color: white;
}

/* .Tabs__Underline {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
