  } */
.tab-content-value-list {
  text-align: center;
  padding: 25px 0px;
}
.tab-content-value-list h5 {
  font-weight: bold;
  font-family: Roboto;
  margin: 0px;
}

.tab-content-value-list p {
  color: #818181;
}

@media only screen and (max-width: 600px) {
  .Tabs__Tab {
    font-size: 14px;
    min-width: 158px;
  }
}
